.section {
    padding-top: $section-padding-y;
    padding-bottom: $section-padding-y;
    &.section-claim {
        color: $section-claim-color;
        background-color: $section-claim-bg;
    }
    &.section-primary {
        color: $section-primary-color;
        background-color: $section-primary-bg;
        + .section-primary {
            border-top-style: solid;
            border-top-width: 1px;
            border-color: $border-color;
        }
    }
    &.section-secondary {
        color: $section-secondary-color;
        background-color: $section-secondary-bg;
    }
    &.section-perex {
        color: $black;
        h2 {
            margin-bottom: 0;
        }
    }
    &.section-center {
        text-align: center;
    }
}

.section-inner {
    .breadcrumb {
        padding-top: 0;
    }
}
